import React, { useContext } from "react";
import { Helmet } from "react-helmet";
import styled, { ThemeContext } from 'styled-components';
import { Text, Box, Image } from 'rebass'
import { ButtonPrimary } from "components/Button";
import okexTestnetCardHeader from '../../assets/images/okex_testnet_card_header.png'
import nutCardHeader from '../../assets/images/nut_card_header.png'
import { Link } from "react-router-dom";

const RowCard = styled(Box)`
  display: flex;
  flex-direction: row;
  align-items: stretch;
  justify-content: flex-start;
  width: 100%;
  max-width: 1200px;
  gap: 2rem;

  ${({ theme }) => theme.mediaWidth.upToSmall`
    flex-direction: column;
    gap: 0rem;
  `};
`

const Card = styled(Box)`
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  background-color: ${({ theme }) => theme.bg3};
  color: ${({ theme }) => theme.text2};
  border-radius: ${({ theme }) => theme.borderRadius1};
  box-shadow: 0 0 8px rgb(0, 0, 0, 0.3);
  max-width: 500px;
  width: 100%;
  overflow: hidden;
  ${({ theme }) => theme.mediaWidth.upToSmall`
   margin-bottom:2rem!important;
`};
`

const Space = styled(Box)`
  display: flex;
  flex-direction: column;
  align-items: inherit;
  justify-content: inherit;
  line-height: 1;
  width: 100%;
  padding: 10px 30px;
`

const CardCover = styled(Image)`
  width: 100%;
`

export default function AirdropLanding() {
  const theme = useContext(ThemeContext)

  return (
    <>
      <Helmet>
        <meta charSet="utf-8" />
        <title>Airdrop - Nut.Money</title>
        <link rel="canonical" href="https://nut.money/#/airdrop" />
        <meta name="description" content="Airdrop - Nut.Money. Become the first owner of NUT Tokens, with up to 300% discount." />
      </Helmet>
      <RowCard mb="40px">
        <Text fontSize={30} fontWeight={600} color={theme.primaryText1} width="100%">
          Airdrop
        </Text>
      </RowCard>
      <RowCard>
      <Card >
          <CardCover src={nutCardHeader} />
          <Space  mt="1em"  mb="3em">
            <Text lineHeight={'20px'} textAlign={'center'}  fontWeight={600} fontSize={24}>
              NUT TOKEN Airdrop 2
            </Text>
            <Text mt="0.5em" textAlign={'center'}  fontSize={14} lineHeight={'20px'}  color={theme.primary1}>
              Apr 22, 2021 - Apr 29, 2021
            </Text>
          </Space>
          <Space  pb="2em">
            <Text textAlign={'center'}  fontSize={14} lineHeight={'20px'} >
              Together for community development, more members own NUT tokens
            </Text>
            <Space mb="0.1em" mt="0.1em"><ButtonPrimary as={Link} to="/airdrop/houbi">
            END
            </ButtonPrimary></Space>
            <Text textAlign={'center'}  fontSize={14} lineHeight={'20px'} >
              1,000,000 NUT have been distributed on this program
            </Text>
          </Space>
        </Card>

        <Card color={theme.text1}>
          <CardCover src={okexTestnetCardHeader} />
          <Space mt="1em" mb="3em">
            <Text textAlign={'center'}  fontWeight={600} fontSize={24}>
              NUT TOKEN Airdrop 1
              </Text>
            <Text mt="0.5em" textAlign={'center'}  fontSize={14} color={theme.primary1}>
              Apr 2, 2021 - Apr 22, 2021
              </Text>
          </Space>
          <Space pb="2em">
            <Text textAlign={'center'}  fontSize={14} lineHeight={'20px'} >
              Stack OKT get NUT on OKEX Testnet. Convert 1 NUT on OKEX to 1 NUT on HECO
              </Text>
              <Space mb="0.5em" mt="0.5em"><ButtonPrimary as="a" href="https://airdrop1.nut.money" target="_blank">END</ButtonPrimary></Space>
            <Text textAlign={'center'}  fontSize={14} lineHeight={'20px'} >
              100,000 NUT have been distributed on this program
              </Text>
          </Space>
        </Card>
       
      </RowCard>
    </>
  )
}