import React, { useContext } from "react";
import { Helmet } from "react-helmet";
import styled, { ThemeContext } from 'styled-components';
import { Text, Box } from 'rebass'
import { Link } from "react-router-dom";
import { ButtonGray } from "components/Button";
import { IoArrowForward } from "react-icons/io5";


const DarkButton = styled(ButtonGray)`
  width: 24em;
  max-width: 100%;
  height: 3rem;
  background-color: ${({ theme }) => theme.bg4};
  font-size: 16px;
`

const RowCard = styled(Box)`
  display: flex;
  flex-direction: row;
  align-items: stretch;
  justify-content: flex-start;
  width: 100%;
  max-width: 1200px;
  gap:2em;
  ${({ theme }) => theme.mediaWidth.upToSmall`
    flex-direction: column;
    gap:0em;
  `};
`

const Card = styled(Box)`
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  background-color: ${({ theme }) => theme.bg3};
  color: ${({ theme }) => theme.text2};
  border-radius: ${({ theme }) => theme.borderRadius1};
  box-shadow: 0 0 8px rgb(0, 0, 0, 0.3);
  padding: 30px;
  max-width: 500px;
  width: 100%;
  ${({ theme }) => theme.mediaWidth.upToSmall`
  margin-bottom:2em!important;
`};
`

const Space = styled(Box)`
  display: flex;
  flex-direction: column;
  align-items: inherit;
  justify-content: inherit;
  line-height: 1;
  width: 100%;
  ${({ theme }) => theme.mediaWidth.upToSmall`
    gap: 0em;
  `};
`

export default function Launchpad() {
  const theme = useContext(ThemeContext)

  return (
    <>
      <Helmet>
        <meta charSet="utf-8" />
        <title>Launchpad - Nut.Money</title>
        <link rel="canonical" href="https://nut.money/#launchpad" />
        <meta name="description" content="Launchpad - Nut.Money. Become the first owner of NUT Tokens, with up to 300% discount." />
      </Helmet>
      <RowCard mb="40px">
        <Text fontSize={30} fontWeight={600} color={theme.primaryText1} width="100%">
          Launchpad
        </Text>
      </RowCard>
      <RowCard>
        <Card color={theme.text1}>
          <Space mb="3em">
            <Text fontWeight={600} lineHeight={'20px'} fontSize={24}>NUT Launchpad</Text>
            <Text mt="0.5em"  textAlign={'center'} fontSize={14} lineHeight={'20px'} color={theme.primary1}>Apr 23, 2021 - Apr 28, 2021 </Text>
          </Space>
          <Space>
            <Text textAlign={'center'} fontSize={14} lineHeight={'20px'}> To become the first owner of NUT Tokens, with a discount of up to 300%. </Text>
            <Space mb="1em" mt="1em"><DarkButton  as={Link} to="/launchpad/nut">END</DarkButton></Space>
            <Text textAlign={'center'} fontSize={14} lineHeight={'20px'}>
              1,000,000 NUT will be distributed on this program
              </Text>
          </Space>
        </Card>
        <Card backgroundColor={theme.primary1} color={theme.primaryText1}>
          <Space mb="3em">
            <Text fontWeight={600} lineHeight={'20px'}  fontSize={24}> Your Launchpad </Text>
            <Text mt="0.5em" textAlign={'center'} lineHeight={'20px'} fontSize={14}> Launch your project with NUT MONEY</Text>
          </Space>
          <Space>
            <Text textAlign={'center'} fontSize={14} lineHeight={'20px'}>NUT accompanies you in developing HECO to become a strong foundation</Text>
            <Space mb="1em" mt="1em"><DarkButton as="a" href="http://bit.ly/nut-cooperation"  target="_blank">Apply Now <IoArrowForward style={{ marginLeft: 10 }}></IoArrowForward></DarkButton></Space>
            <Text textAlign={'center'} fontSize={14} lineHeight={'20px'}>
              The NUT team will contact you to verify the project
            </Text>
          </Space>
        </Card>
      </RowCard>
    </>
  )
}