import React, {useState} from 'react'
import { AutoColumn } from '../../components/Column'
import styled from 'styled-components'
import { STAKING_REWARDS_INFO, useStakingInfo } from '../../state/stake/hooks'
import { TYPE, ExternalLink } from '../../theme'
import PoolCard from '../../components/earn/PoolCard'
import { RowBetween } from '../../components/Row'
import { CardSection, DataCard, Close } from '../../components/earn/styled'
import Loader from '../../components/Loader'
import { useActiveWeb3React } from '../../hooks'
//import { JSBI } from '../../libs/sdk' // '@uniswap/sdk'
//import { BIG_INT_ZERO } from '../../constants'
import { OutlineCard } from '../../components/Card'
import { CONFIG } from '../../constants/index';
import {Helmet} from "react-helmet";

const PageWrapper = styled(AutoColumn)`
  max-width: 640px;
  width: 100%;
`

const TopSection = styled(AutoColumn)`
  max-width: 720px;
  width: 100%;
`

const PoolSection = styled.div`
  display: grid;
  grid-template-columns: 1fr;
  column-gap: 10px;
  row-gap: 15px;
  width: 100%;
  justify-self: center;
`


export default function Earn() {
  const { chainId } = useActiveWeb3React()
  const [ShowNotificaion,setShowNotification] = useState(():Boolean=>{
    return localStorage.notificationEarn ? false :true;
   });
   
  // staking info for connected account
  const stakingInfos = useStakingInfo()

  /**
   * only show staking cards with balance
   * @todo only account for this if rewards are inactive
   */
  //const stakingInfosWithBalance = stakingInfos?.filter(s => JSBI.greaterThan(s.stakedAmount.raw, BIG_INT_ZERO))
  const stakingInfosWithBalance = stakingInfos;

  // toggle copy if rewards are inactive
  const stakingRewardsExist = Boolean(typeof chainId === 'number' && (STAKING_REWARDS_INFO[chainId]?.length ?? 0) > 0)
  // console.log(stakingRewardsExist);
  function closeNotification (){
    setShowNotification(false);
    localStorage.notificationEarn = false;
    }


  return (
    <>
    <Helmet>
        <meta charSet="utf-8" />
        <title>Earning - Nut.Money</title>
        <link rel="canonical" href="https://nut.money/#earn" />
        <meta name="description" content="Deposit your Liquidity Provider (NUT - LP) tokens to earn NUT Token." />
      </Helmet>
    <PageWrapper gap="lg" justify="center">
      <TopSection gap="md">
        <DataCard>
          {/* <CardBGImage />
          <CardNoise /> */}
          <CardSection style={{display: ShowNotificaion?'block':'none'}}>
            <AutoColumn gap="md">
              <RowBetween>
                <TYPE.white  fontSize={20} fontWeight={600}>Liquidity mining</TYPE.white>
                <Close 
                 onClick={() => {
                  closeNotification();
                }}
                />
              </RowBetween>
              <RowBetween>
                <TYPE.white fontWeight={300} fontSize={14}>
                Stake Liquidity Pool (NUT - LP) tokens to earn {CONFIG['symbol']} Token.
                </TYPE.white>
              </RowBetween>{' '}
              <ExternalLink
                style={{ color: 'white'}}
                href='https://docs.nut.money/products/earn-nut'
                target="_blank"
              >
                <TYPE.white fontSize={14}>Read more about Liquidity mining </TYPE.white>
              </ExternalLink>
            </AutoColumn>
          </CardSection>
          {/* <CardBGImage />
          <CardNoise /> */}
        </DataCard>
      </TopSection>

      <AutoColumn gap="lg" style={{ width: '100%', maxWidth: '720px' }}>
   

        <PoolSection>
          {stakingRewardsExist && stakingInfos?.length === 0 ? (
            <Loader style={{ margin: 'auto' }} />
          ) : !stakingRewardsExist ? (
            <OutlineCard>No active pools</OutlineCard>
          ) : stakingInfos?.length !== 0 && stakingInfosWithBalance.length === 0 ? (
            <OutlineCard>No active pools</OutlineCard>
          ) : (
            stakingInfosWithBalance?.map(stakingInfo => {
              // need to sort by added liquidity here
              return <PoolCard key={stakingInfo.stakingRewardAddress} stakingInfo={stakingInfo} />
            })
          )}
        </PoolSection>
      </AutoColumn>
    </PageWrapper>
    </>
  )
}
