import React from 'react'
import styled from 'styled-components'
import Logo from '../../assets/svg/Logo_nut.svg'
import Lovo_Chain from '../../assets/svg/chain.svg'
import Logo_airdrop from '../../assets/svg/airdrop.svg'
import Cover from '../../assets/svg/cover.svg'
import Cover1 from '../../assets/svg/cover1.svg'
import Cover2 from '../../assets/svg/cover2.svg'
import { TYPE } from '../../theme'
import Column, { AutoColumn } from '../../components/Column'
import { Text, Flex } from 'rebass'
import {Helmet} from "react-helmet";

// import { Type } from 'react-feather'

// import { STAKING_REWARDS_INFO, useStakingInfo } from '../../state/stake/hooks'
