const PageWrapper = styled(AutoColumn)`
  max-width: 1240px;
  width: 100%;
  ${({ theme }) => theme.mediaWidth.upToExtraSmall`
    max-width: none;
    display: block;
    `};
`
const SelectHeader = styled(AutoColumn)`
  width: 100%;
  display: flex;
  min-height: 360px;
  ${({ theme }) => theme.mediaWidth.upToExtraSmall`
        display: block;
    `};
`
const UniIcon = styled.div`
  img {
    width: auto;
    height: 34px;
  }
`

const IconOkex = styled.div`
  align-items: center;
  display: inline-flex;
  span {
    font-size: 15px;
    font-weight: bold;
    margin-left: 8px;
    color: #fff;
  }
  ${({ theme }) => theme.mediaWidth.upToExtraSmall`
    margin-top: 20px;
  `};
`
const ColumnHead = styled.div`
  display: initial;
  flex: 0 0 50%;
  padding-left: 0px;
`
const ColumnHead2Mobile = styled(ColumnHead)`
  ${({ theme }) => theme.mediaWidth.upToExtraSmall`
    display:none
`};
`
const UniIconRight = styled.div`
  position: absolute;
  top: -178px;
  left: 61px;
  z-index: 9;
  width: auto;
  height: auto;
`
const TotalNut = styled.div`
  margin-top: 15px;
  font-size: 22px;
  color: #fff;
  font-weight: 600;
  ${({ theme }) => theme.mediaWidth.upToExtraSmall`
        margin-top: 22px;
        font-size: 17px;
    `};
  span {
    font-size: 58px;
    color: #f9603e;
    font-weight: 400;
    margin: 0px 8px;
    ${({ theme }) => theme.mediaWidth.upToExtraSmall`
            font-size: 24px;
            font-weight:bold;
        `};
  }
`
const SelectTask = styled.div`
  width: 100%;
`
const ListTaskNut = styled.li`
  display: flex;
  height: 102px;
  margin-bottom: 20px;
  font-size: 15px;
  padding: 15px 31px;
  align-items: center;
  color: #fff;
  background-position: center;
  border: 0.1px solid #1c3d39;
  border-radius: 24px;
  ${({ theme }) => theme.mediaWidth.upToExtraSmall`
        background-image: none !important;
        background: #29314a;
        border: none;
        height: auto;
        display: block;
        padding:0;
    `};
`
const BoxTaskNutLeft = styled.div`
  flex: 0 0 30%;
  display: flex;
  align-items: center;
  ${({ theme }) => theme.mediaWidth.upToExtraSmall`
        padding: 15px 24px;
        border-bottom: 1px solid #494f5f;
    `};
  div {
    width: 50px;
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: 700;
    font-size: 26px;
    border-radius: 50%;
    ${({ theme }) => theme.mediaWidth.upToExtraSmall`
            width: 45px;
            height: 45px;
        `}
  }
  span {
    font-size: 24px;
    font-weight: 300;
    margin-left: 20px;
    ${({ theme }) => theme.mediaWidth.upToExtraSmall`
            font-size: 23px;
            font-weight: 700;
            margin-left: 11px;

        `}
  }
`
const BoxTaskNutCenter = styled.div`
  flex: 0 0 35%;
  font-weight: 300;
  ${({ theme }) => theme.mediaWidth.upToExtraSmall`
        padding: 21px 24px;
        text-align: center;
    `};
`
const BoxTaskNutRight = styled.div`
  flex: 0 0 35%;
  text-align: right;
  div {
    display: inline-flex;
    width: 190px;
    height: 42px;
    align-items: center;
    justify-content: center;
    border-radius: 23px;
  }
  ${({ theme }) => theme.mediaWidth.upToExtraSmall`
        text-align: center;
        padding: 15px 24px 20px;
    `};
`
const ButtonClaim = styled.div`
  color:#fff;
`
const HeaderButtonClaim = styled.div`
  color:#fff;
  text-algin:center;
  div{
      background: rgb(241, 109, 79);
      display: inline-flex;
      width: 190px;
      height: 42px;
      align-items: center;
      justify-content: center;
      border-radius: 5px;
      margin:20px auto 0px;
      font-weight:auto;
  }
  div:hover{
    background: rgb(255, 89, 44);
    transition: all 0.2s;
  }
`
const ContentHeadNut = styled.div`
  font-size: 15px;
  line-height: 26px;
  margin-top: 35px;
`
const TopSection = styled(AutoColumn)`
   width: 100%;
 `
 const ImageStyle =  styled.div`
    text-align:center;
    padding:10px;
    img{
      margin:0 auto;
      ${({ theme }) => theme.mediaWidth.upToExtraSmall`
      width:100%;
    `};
    }
`

